html {
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: hsl(0, 0%, 16%);
}

.container {
  background-color: #c785bb;
  max-width: 600px;
  padding: 10px 20px 20px;
  border-radius: 20px;
  margin: 0 auto 40px;

}

h1 {
  text-align: center;
  padding-top: 25px;
  padding: 0.2em;
  background-color: #c785bb;
  color: white;
}

footer {
  text-align: center;
  padding-top: 25px;
  padding: 0.2em;
  background-color: #c785bb;
  color: white;

}

.container h2 {
  margin: 0 auto;
  text-align: center;
  padding: 10px 0 20px 0;
  color: white;
  
}

button {
  background: rgb(211, 216, 253);
  color: #c785bb;
  border: 2px solid #9588FF;
  border-radius: 5px;
  font-size: 20px;
}

.picture {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 40px;
  
}

.picture img {
  border-radius: 20px;
  max-width: 100%;
}

section div,
section p {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  margin-top: 0px;
}

section p {
  padding-bottom: 20px;
}

section > div.closed {
  margin-bottom: 20px;
  box-shadow: 5px 10px #FFE6FF;
}
